<template>
  <v-layout
    align-center
    row
    wrap
  >
    <v-flex xs12>
      <v-layout
        align-center
        justify-center
        class="banner"
      >
        <v-flex xs11>
          <h1 class="headerline">What's In Your Pet Food?</h1>
          <InputType
            @toggleCam="toggleCam"
            @search="search"
          ></InputType>
        </v-flex>
      </v-layout>
    </v-flex>
    <v-flex
      xs12
      v-show="openCam"
    >
      <v-dialog
        v-model="openCam"
        width="500"
        class="dialog"
      >
        <v-toolbar
          light
          color="white"
          class="dialog-title"
          style="font-size: 2em;"
        >
          Take Photo of the ingredient label
          <v-spacer></v-spacer>
          <v-btn
            icon
            @click="toggleCam"
          >
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card>
          <v-divider></v-divider>
          <WebCam
            ref="webcam"
            :deviceId="deviceId"
            width="500px"
            height="100%"
            @cameras="onCameras"
            @camera-change="onCameraChange"
            @capturedImages="onCapturedImages"
            :isFrontCam="frontCam"
            :googleKey="googleKey"
          >
          </WebCam>
          <v-card-actions style="background:#00c06d; color:white;">
            <v-btn
              color="white"
              flat
              @click="capture"
              style="margin:0 auto; width: 100%;"
            >
              SNAP IT!
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </v-flex>
    <v-flex
      xs12
      v-if="this.captures.length > 0"
    >
      <v-layout
        row
        style="padding: 10px"
      >
        <v-card
          xs12
          style="padding: 10px"
        >
          <v-layout
            row
            wrap
            justify-space-around
          >
            <v-flex xs12>
              <h3
                class="headline font-weight-light text-capitalize"
                style="padding:10px"
              >captured images</h3>
            </v-flex>
            <v-flex
              xs3
              v-for="(capture,i) in captures"
              :key="`id_${i}_${capture.img}`"
              class="captured-image"
            >
              <img
                :class="currentIndex == i ? 'hightlight' : ''"
                :src="capture.image"
                style="width:100%"
                @click="pickReport(i) "
                :alt="`${fileSize(capture.image)} kb`"
              />
              <v-icon
                color="green"
                v-show="currentIndex == i"
              >check_circle</v-icon>
            </v-flex>
          </v-layout>
        </v-card>

      </v-layout>
    </v-flex>

    <v-flex
      xs12
      v-if="this.lastSearchType == 'img' && Object.keys(imgReport).length > 0"
    >
      <v-layout
        row
        style="padding: 10px"
      >
        <v-card
          xs12
          style="padding: 10px"
        >
          <v-layout
            row
            wrap
            justify-space-around
          >
            <v-flex xs12>
              <h3
                class="headline font-weight-light text-capitalize"
                style="padding:10px"
              >Results</h3>
            </v-flex>
            <ImageResult :report="imgReport"></ImageResult>
          </v-layout>
        </v-card>

      </v-layout>
    </v-flex>
    <v-flex
      xs12
      v-if="this.lastSearchType == 'text' && keyword !== ''"
    >
      <v-layout
        row
        style="padding: 10px"
      >
        <v-card
          xs12
          style="padding: 10px"
        >
          <v-layout
            row
            wrap
            justify-space-around
          >
            <v-flex xs12>
              <h3
                class="headline font-weight-light text-capitalize"
                style="padding:10px"
              >Text Results</h3>
            </v-flex>
            <TextResult :text="keyword"></TextResult>
          </v-layout>
        </v-card>

      </v-layout>
    </v-flex>
    <v-flex
      xs12
      v-else
    >
      <v-layout
        row
        wrap
        class="container"
      >
        <v-flex xs12>
          <div class="display-5 font-weight-medium"> No results found! </div>
        </v-flex>
      </v-layout>
    </v-flex>
  </v-layout>
</template>

<script>
import InputType from './InputType.vue'
// import Cam from './Cam.vue'
import { WebCam } from 'vue-cam-vision'
import ImageResult from '@/components/ImageResult'
import TextResult from '@/components/TextResult'

export default {
  name: 'home',
  components: {
    InputType,
    WebCam,
    ImageResult,
    TextResult
  },
  data () {
    return {
      openCam: false,
      googleKey: process.env.VUE_APP_OCR_GVA,
      captures: [],
      imgReport: [],
      frontCam: false,
      img: null,
      camera: null,
      deviceId: null,
      devices: [],
      currentIndex: 0,
      keyword: '',
      lastSearchType: ''
    }
  },
  watch: {
    openCam: function (value) {
      if (!value) {
        this.$refs.webcam.stop()
        // eslint-disable-next-line
        console.log('close')
      } else {
        this.$refs.webcam.start()
      }
    },
    camera: function (id) {
      this.deviceId = id;
    },
    devices: function () {
      // Once we have a list select the first one
      let first = this.devices || this.devices[0]
      if (first) {
        this.camera = first.deviceId
        this.deviceId = first.deviceId
      }
    }
  },
  methods: {
    pickReport (i) {
      this.imgReport = this.captures[i].imgReport
      this.currentIndex = i
      this.lastSearchType = 'img'
    },
    toggleCam () {
      this.openCam = !this.openCam
    },
    search (text) {
      // eslint-disable-next-line
      console.log(`search ${text}`)
      this.keyword = text
      this.lastSearchType = 'text'
    },
    async sendGVision (type = 'DOCUMENT_TEXT_DETECTION') {
      const report = await this.$refs.webcam.googleVision(type)
      // eslint-disable-next-line
      console.log(report)
      this.imgReport = report
    },
    async capture () {
      this.img = await this.$refs.webcam.capture()
      this.sendGVision()
      this.toggleCam()
    },
    onCameras (cameras) {
      this.devices = cameras
      // eslint-disable-next-line
      console.log('On Cameras Event', cameras)
    },
    onCameraChange (deviceId) {
      this.deviceId = deviceId
      this.camera = deviceId
      // eslint-disable-next-line
      console.log('On Camera Change Event', deviceId)
    },
    onCapturedImages (captures) {
      this.captures = captures
      // eslint-disable-next-line
      console.log(captures)
      // eslint-disable-next-line
      console.log('captures')
      this.currentIndex = this.captures.length - 1
    },
    fileSize (URL) {
      const stringLength = URL.length - 'data:image/png;base64,'.length;
      const sizeInBytes = 4 * Math.ceil((stringLength / 3)) * 0.5624896334383812
      const sizeInKb = sizeInBytes / 1000
      // eslint-disable-next-line
      console.log(sizeInKb)
      return sizeInKb
    }
  }
}
</script>
